import {Dispatch} from "redux";
import {authAPI, securityAPI} from "api/api";
import {AppThunk} from "./redux-store";
import {stopSubmit} from "redux-form";

export type AuthStateType = {
    userId: string | null
    email: string | null
    login: string | null
    isAuth: boolean
    captchaUrl: string | null
}

let initialState: AuthStateType = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null
}

export const authReducer = (state = initialState, action: AuthRedActionsType): AuthStateType => {
    switch (action.type) {
        case 'samurai-js/auth/SET-USER-DATA': {
            return {...state, ...action.payload}
        }
        case 'samurai-js/auth/GET-CAPTCHA-URL-SUCCESS': {
            return {...state, ...action.payload}
        }
        default:
            return state
    }
}

export type setUserDataACType = ReturnType<typeof setAuthUserDataAC>
export const setAuthUserDataAC = (userId: string | null, email: string | null, login: string | null, isAuth: boolean) => {
    return {
        type: 'samurai-js/auth/SET-USER-DATA',
        payload: {
            userId,
            email,
            login,
            isAuth,
        }
    } as const
}

export type getCaptchaUrlSuccessACType = ReturnType<typeof getCaptchaUrlSuccessAC>
export const getCaptchaUrlSuccessAC = (captchaUrl: string | null) => {
    return {
        type: 'samurai-js/auth/GET-CAPTCHA-URL-SUCCESS',
        payload: {
            captchaUrl
        }
    } as const
}

export type AuthRedActionsType = setUserDataACType | getCaptchaUrlSuccessACType


export const getAuthUserData = () => async (dispatch: Dispatch) => {
    const response = await authAPI.me()
    if (response.data.resultCode === 0) {
        let {id, email, login} = response.data.data
        dispatch(setAuthUserDataAC(id, email, login, true))
    }
}

export const login = (email: string, password: string, rememberMe: boolean, captcha: string | null): AppThunk => async (dispatch) => {
    const response = await authAPI.login(email, password, rememberMe, captcha)
    if (response.data.resultCode === 0) {
        dispatch(getAuthUserData())
    } else {
        if (response.data.resultCode === 10) {
            dispatch(getCaptchaUrl())
        }
        let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error'
        dispatch(stopSubmit('login', {_error: message}))
    }
}

export const getCaptchaUrl = (): AppThunk => async (dispatch) => {
    const response = await securityAPI.getCaptchaUrl()
    const captchaUrl = response.data.url
    dispatch(getCaptchaUrlSuccessAC(captchaUrl))
}

export const logout = () => async (dispatch: Dispatch) => {
    const response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserDataAC(null, null, null, false))
    }
}