import React from 'react';
import {InjectedFormProps, reduxForm} from "redux-form";
import {createField, Input} from "../common/FormsControls/FormsControls";
import {required} from "utils/validators/validators";
import {connect} from "react-redux";
import {login} from "redux/authReducer";
import {Redirect} from "react-router-dom";
import {AppStateType} from "redux/redux-store";
import styles from "../common/FormsControls/FormsControls.module.css";

type FormDataType = {
    email: string
    password: string
    rememberMe: boolean
    captcha: string | null
}

type LoginFormProps = {
    captchaUrl: string | null
}
type Props = InjectedFormProps<FormDataType, LoginFormProps> & LoginFormProps

const LoginForm = ({handleSubmit, error, captchaUrl}: Props) => {
    return (
        <form onSubmit={handleSubmit}>
            {createField('Email', 'email', [required], Input)}
            {createField('Password', 'password', [required], Input, {type: 'password'})}
            {createField(null, 'rememberMe', [], Input, {type: 'checkbox'}, "remember me")}
            {captchaUrl && <img src={captchaUrl}/>}
            {captchaUrl
                &&
                createField('Symbols from image', 'captcha', [required], Input, {})
            }
            {
                error
                &&
                <div className={styles.formSummaryError}>
                    {error}
                </div>
            }
            <div>
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm<FormDataType, LoginFormProps>({form: 'login'})(LoginForm)

const Login = (props: MapStateToPropsType & MapDispatchToPropsType) => {
    const onSubmit = (formData: FormDataType) => {
        props.login(formData.email, formData.password, formData.rememberMe, formData.captcha)
    }

    if (props.isAuth) {
        return <Redirect to={'/profile'}/>
    }

    return (
        <div>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl}/>
        </div>
    );
};
type MapStateToPropsType = {
    captchaUrl: string | null
    isAuth: boolean,
}

type MapDispatchToPropsType = {
    login: (email: string, password: string, rememberMe: boolean, captcha: string | null) => void
}

const mapStateToProps = (state: AppStateType): MapStateToPropsType => ({
    captchaUrl: state.auth.captchaUrl,
    isAuth: state.auth.isAuth,
})

export default connect(mapStateToProps, {login})(Login);